import { useState,useEffect } from "react";
import { Form, Button, Modal } from "react-bootstrap";
// import { useNavigate } from "react-router-dom";
import { loginServer } from "@/services/login";
import "./Login.css";
const Login = (props: any) => {
  const [validated, setValidated] = useState(false);
  const [userName] = useState("test");
  const [password] = useState("test");
  const [error, setError] = useState("");
  // let navigate=useNavigate();

  useEffect(()=>{
      setError('')
  },[props.show])

  // 
  const handleLogin = (event: any) => {
    event.preventDefault();
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.stopPropagation();
    }
    loginServer({
      username: form.formUsername.value,
      password: form.formPassword.value,
    })
      .then((res) => {
        window.localStorage.setItem("loggerData", JSON.stringify(res));
        props.change(res?.username??'');
        setValidated(true);
        setError('');
        props.onHide();
      })
      .catch((err: any) => {
        console.log(err.error);
        setError(err.error);
      });
  };

  // const register=()=>{
  //   navigate('/register');
  //   props.onHide()
  // }
  return (
    <Modal {...props} centered fullscreen="md-down">
      <Modal.Header closeButton closeLabel="Update">
        <Modal.Title id="contained-modal-title-vcenter"></Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form validated={validated} onSubmit={handleLogin}>
          <Form.Group className="mb-3" controlId="formUsername">
            <Form.Label></Form.Label>
            <Form.Control
              type="text"
              placeholder=""
              required
              defaultValue={userName}
            />
            <Form.Control.Feedback type="invalid">
              !
            </Form.Control.Feedback>
          </Form.Group>
          <Form.Group className="mb-3" controlId="formPassword">
            <Form.Label></Form.Label>
            <Form.Control
              type="password"
              placeholder=""
              required
              defaultValue={password}
            />
            <Form.Control.Feedback type="invalid">
              !
            </Form.Control.Feedback>
          </Form.Group>
          {error ? <p className="fs-6 text-danger">{error}</p> : <br />}
          <div className="d-grid gap-2">
            <Button className="btn-info btn-round shadow" type="submit">
              
            </Button>
          </div>
        </Form>
      </Modal.Body>
    </Modal>
  );
};
export default Login;

